





















import ut from '../../utils/index.js';
export default {
	data() {
		return {
			list:[],
			src:''
		}
	},
	onLoad(opt) {
		this.src=opt.src;
	},
	onShow() {
		this.req_getAdress();
	},
	methods: {
		setadress(item){
			if(this.src){
				wx.setStorageSync('adress',item);
				wx.navigateBack();
			}
		},
		go_mine_addadress(id){
			id=id||''
			wx.navigateTo({
				url: '../mine/addadress?_id='+id
			})
		},
		req_getAdress(){
			console.log(this.list)
			ut.request({
				url: "address/list"
			}).then(data => {
				this.list=data;
			})
		},
		req_setDef(item){
			ut.request({
				data:{
				  id: item.id
				},
				url: "address/setAttr"
			}).then(data => {
				this.req_getAdress();
			})
		},
		req_del(item,index){
			ut.request({
				data:{
				id: item.id
				},
				url: "address/delete"
			}).then(data => {
				this.req_getAdress();
			})
		}
	}
	
}
